import { Component, EventEmitter, Input, Output, OnChanges, OnInit } from '@angular/core';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { IComponentFormError } from '../../interfaces';
import { IRadioInputOption } from '../../interfaces/components/radioInuptOption.interface';
@Component({
  selector: 'custom-radio-group-form',
   imports: [CustomAppErrorComponent, ReactiveFormsModule],
  templateUrl: './custom-radio-group-form.component.html',
  styleUrl: './custom-radio-group-form.component.css'
})
export class CustomRadioGroupFormComponent implements OnInit {
  @Input() setFirstAsDefaultValue: boolean = true;
  @Input({ required: true }) name: string = '';
  @Input({ required: true }) controlName: string = '';
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) validation: IComponentFormError[] = [];
  @Input({ required: true }) options: IRadioInputOption[] = [];


  @Output() onChange = new EventEmitter<IRadioInputOption>();

  ngOnInit(){
    const control = this.parentForm.get(this.controlName);
    if(!control?.value){

      if(this.setFirstAsDefaultValue && this.options.length > 0) {
        this.parentForm.get(this.controlName)?.setValue(this.options[0].value);
      }
    }

  }

  onRadioChange(event : IRadioInputOption): void {
    this.onChange.emit(event);
  }
}
