import {
  Component,
  EventEmitter,
  Input,
  Output,
  TemplateRef,
} from '@angular/core';
import { IStatusCol, ITableCategory, ITableHeader } from '../../interfaces';
import { CommonModule } from '@angular/common';
import {
  actionDeleteSvg,
  actionEditSvg,
  actionViewSvg,
  sortSvg,
} from './assets';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Component({
  selector: 'custom-table',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './custom-table.component.html',
  styleUrl: './custom-table.component.css',
})
export class CustomTableComponent<T> {
  path = '../../../../src/public/gear-icon.png';
  @Input({ required: false }) tableData!: T[] | null;
  @Input({ required: false }) tableCategories!: ITableCategory<T>[] | null;
  @Input({ required: true }) tableHeader!: ITableHeader<T>[];
  @Input({ required: true }) showStatusColumn!: boolean;
  @Input({ required: true }) showActionColumn!: boolean;
  @Input() statusCol: IStatusCol<T> = {
    header: 'status' as keyof T,
    trueValue: true,
    trueText: 'Active',
    falseText: 'InActive',
    sort: false,
  };
  @Input() rowClass: string = '';
  @Input() headerClass: string = '';
  @Input() templates: { [key: string]: TemplateRef<any> } = {};
  @Input() enableEdit: boolean = true;
  @Input() enableDelete: boolean = true;
  @Input() enableView: boolean = true;
  @Output() onEdit = new EventEmitter<T>();
  @Output() onView = new EventEmitter<T>();
  @Output() onDelete = new EventEmitter<T>();
  @Output() onRowClick = new EventEmitter<T>();
  @Output() sortColumn = new EventEmitter<
    string | number | symbol | undefined
  >();

  checkedSortIcon: SafeHtml;
  checkedActionViewSvg: SafeHtml;
  checkedActionEditSvg: SafeHtml;
  checkedActionDeleteSvg: SafeHtml;

  constructor(private sanitizer: DomSanitizer) {
    const sortSvgIcon = sortSvg;
    this.checkedSortIcon = this.sanitizer.bypassSecurityTrustHtml(sortSvgIcon);
    const ActionView = actionViewSvg;
    this.checkedActionViewSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionView);
    const ActionEdit = actionEditSvg;
    this.checkedActionEditSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionEdit);
    const ActionDelete = actionDeleteSvg;
    this.checkedActionDeleteSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionDelete);
  }

  ngOnInit() {
    if (!this.tableHeader) {
      throw new Error('tableHeader input is required.');
    }
    if (!this.tableData && !this.tableCategories) {
      throw new Error('You need to log tableCategories or tableData');
    }
  }
}
