import { Component, input, Input, signal } from '@angular/core';
import { AbstractControl } from '@angular/forms';
import { IComponentFormError } from '../../interfaces';
import { TranslateModule } from '@ngx-translate/core';

@Component({
  selector: 'custom-app-error',
  imports: [TranslateModule],
  standalone: true,
  templateUrl: './custom-app-error.component.html',
  styleUrls: ['./custom-app-error.component.css'],
})
export class CustomAppErrorComponent {
  @Input({required : true}) control!: AbstractControl;
  @Input({required : true}) validation: IComponentFormError[] = [];
  @Input({required : true}) name: string = '';
   showErrors = input<boolean>(false);

  shouldShowError(item: IComponentFormError): boolean {
    return item.errorType.some(error =>
      this.control.hasError(error) &&
      this.control.invalid &&
      (this.control.touched || this.control.dirty) || this.showErrors()
    );
  }

  getErrorMessage(item: IComponentFormError): string {
    const activeError = item.errorType.find(error => this.control.hasError(error));
    return activeError ? item.errorMessage : '';
  }
}
