import { CommonModule } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IDropdownOption } from '../../interfaces/';
import { ClickOutsideDirective } from '../../directives';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { dropdownArrowSvg } from './assets';

@Component({
  selector: 'custom-dropdown-button',
  imports: [CommonModule, ClickOutsideDirective],
  templateUrl: './custom-dropdown-button.component.html',
  styleUrl: './custom-dropdown-button.component.css',
})
export class CustomDropdownButtonComponent {
  @Input({ required: true }) options: IDropdownOption[] = [];
  @Output() selected = new EventEmitter<IDropdownOption>();

  dropdownArrow!: SafeHtml;
  isOpen = false;

  constructor(private sanitizer: DomSanitizer) {
    const dropdownArrowSVGIcon = dropdownArrowSvg;
    this.dropdownArrow =
      this.sanitizer.bypassSecurityTrustHtml(dropdownArrowSVGIcon);
  }

  toggleDropdown() {
    this.isOpen = !this.isOpen;
  }

  handleSelect(option: IDropdownOption) {
    this.selected.emit(option);
    this.isOpen = false;
  }
}
