import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  signal,
  SimpleChanges,
} from '@angular/core';
import { IStepConfig } from '../../interfaces/components/steppers.interface';
import { NgClass, NgComponentOutlet } from '@angular/common';
import { StepperService } from '../../services/ui/stepper.service';
import { checkedIconSrc } from './assets';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { TranslateModule } from '@ngx-translate/core';
import { CustomButtonComponent } from '../custom-button/custom-button.component';

@Component({
  selector: 'custom-steppers-container',
  imports: [NgComponentOutlet, NgClass , TranslateModule , ],
  templateUrl: './custom-steppers-container.component.html',
  styleUrl: './custom-steppers-container.component.css',
})
export class CustomSteppersContainerComponent implements OnChanges {
  checkedIcon: SafeHtml;
  @Input() steps: IStepConfig[] = [];

  constructor(
    private stepperService: StepperService,
    private sanitizer: DomSanitizer
  ) {
    const checkSvgString = checkedIconSrc;
    this.checkedIcon = this.sanitizer.bypassSecurityTrustHtml(checkSvgString);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['steps']) {
      this.stepperService.setTotalSteps(this.steps.length); // Set total steps when steps change
    }
  }

  get currentStep(): number {
    return this.stepperService.currentStep();
  }

  get currentStepIndex(): number {
    return this.steps.findIndex((step) => step.value === this.currentStep);
  }

}
