import { Component, input, Input, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { CustomInputFormComponent } from '../custom-input-form/custom-input-form.component';
import { CustomButtonComponent } from '../custom-button/custom-button.component';
import { TranslateModule } from '@ngx-translate/core';
import { ComponentFormErrorConstant } from '../../enums';
import { IComponentFormError } from '../../interfaces';
@Component({
  selector: 'custom-fields-form',
  imports: [ReactiveFormsModule, CustomInputFormComponent , CustomButtonComponent , TranslateModule],
  templateUrl: './custom-fields-form.component.html',
  styleUrl: './custom-fields-form.component.css'
})
export class CustomFieldsFormComponent implements OnInit {
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) controlName!: string;
  @Input() addTitle: string = 'Add Custom Field';
  addOneField = input<boolean>(false)

   requiredField: IComponentFormError[] = [
    {
      errorType: [ComponentFormErrorConstant.REQUIRED],
      errorMessage: "Field is required",
    },
  ];

  constructor(private fb: FormBuilder) {}

  get fieldsFormArray(): FormArray {
    return this.parentForm.get(this.controlName) as FormArray;
  }
 get fieldsFormArrayControllers(): FormGroup[] {
  const formArray = this.parentForm.get(this.controlName) as FormArray;
  return formArray.controls as FormGroup[];
}

  ngOnInit(): void {
    // Ensure the FormArray is initialized
    if (!this.parentForm.get(this.controlName)) {
      this.parentForm.addControl(this.controlName, this.fb.array([]));
    }
  }

  addField() {
    const group = this.fb.group({
      key: ['', Validators.required],
      value: ['' , Validators.required]
    });
    this.fieldsFormArray.push(group);
  }

  removeField(index: number) {
    this.fieldsFormArray.removeAt(index);
  }
}
