import { Component, EventEmitter, inject, input, Output } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { CustomButtonComponent } from '../custom-button/custom-button.component';
import { StepperService } from '../../services';

@Component({
  selector: 'custom-steppers-controllers',
  imports: [CustomButtonComponent, TranslateModule ],
  templateUrl: './custom-steppers-controllers.component.html',
  styleUrl: './custom-steppers-controllers.component.css',
})
export class CustomSteppersControllersComponent {
  stepperService = inject(StepperService);
  disabledNext = input<boolean>(true)
  nextLabel = input<string>('')
  prevLabel = input<string>('')
  cancelLabel = input<string>('')
  sendLabel = input<string>('')

  @Output() onNext = new EventEmitter<boolean>();
  @Output() onPerv = new EventEmitter<boolean>();
  @Output() onCancel = new EventEmitter<boolean>();
  @Output() onSubmit = new EventEmitter<boolean>();

  nextStep() {
   this.onNext.emit(true)
  }
  previousStep() {
    this.onPerv.emit(true)
  }
  cancel() {
    this.onCancel.emit(true)
    this.stepperService.notifyCancel$.next(true);
  }
  submit() {
    this.onSubmit.emit(true)
  }
}
