import { Component, Input, Output, EventEmitter } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

// const bala = {
//   attachments: [
//     {
//       id: 'eb15c088-6834-4929-b6a0-4fbf0643cacb',
//       fileName: 'Screenshot 2025-05-26 at 10.45.08PM.png',
//       mimeType: 'image/png',
//       uploadTime: '2025-06-23T10:53:10Z',
//       base64Content: '187 KB',
//     },
//   ],
// };
export interface FileData {
  id: string;
  fileName: string;
  mimeType: string;
  base64Content: string;
  uploadTime?: string;
  fileSize: string;
}
@Component({
  selector: 'custom-file-viewer',
  imports: [CommonModule],
  templateUrl: './custom-file-viewer.component.html',
  styleUrl: './custom-file-viewer.component.css',
})
export class CustomFileViewerComponent {
  @Input({ required: true }) file!: FileData;
  @Input() showMenu: boolean = true;
  @Output() menuClick = new EventEmitter<FileData>();
  @Output() fileClick = new EventEmitter<FileData>();
  showTooltip = false;

  private mimeTypesMap: { [key: string]: string } = {
    'application/pdf': 'PDF',
    'application/vnd.ms-powerpoint': 'PPT',
    'application/vnd.openxmlformats-officedocument.presentationml.presentation':
      'PPT',
    'application/vnd.ms-excel': 'XLS',
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': 'XLS',
    'application/msword': 'DOC',
    'application/vnd.openxmlformats-officedocument.wordprocessingml.document':
      'DOC',
    'image/png': 'PNG',
    'image/jpeg': 'JPG',
    'image/gif': 'GIF',
  };

  constructor(private sanitizer: DomSanitizer) {}

  getFileTypeLabel(): string {
    return this.mimeTypesMap[this.file.mimeType] || 'FILE';
  }

  getFileIcon(): SafeHtml {
    const fileType = this.getFileTypeLabel();

    switch (fileType) {
      case 'PPT':
        return this.sanitizer
          .bypassSecurityTrustHtml(`<svg width="48" height="48" viewBox="0 0 48 48" fill="none">
          <rect width="48" height="48" rx="12" fill="#FF6B4A"/>
          <path d="M14 16h20v16H14z" fill="white" fill-opacity="0.2"/>
          <text x="24" y="28" text-anchor="middle" font-size="10" fill="white" font-weight="bold">PPT</text>
        </svg>`);

      case 'PDF':
        return this.sanitizer
          .bypassSecurityTrustHtml(`<svg width="48" height="48" viewBox="0 0 48 48" fill="none">
          <rect width="48" height="48" rx="12" fill="#dc2626"/>
          <path d="M14 16h20v16H14z" fill="white" fill-opacity="0.2"/>
          <text x="24" y="28" text-anchor="middle" font-size="10" fill="white" font-weight="bold">PDF</text>
        </svg>`);

      case 'DOC':
        return this.sanitizer
          .bypassSecurityTrustHtml(`<svg width="48" height="48" viewBox="0 0 48 48" fill="none">
          <rect width="48" height="48" rx="12" fill="#2563eb"/>
          <path d="M14 16h20v16H14z" fill="white" fill-opacity="0.2"/>
          <text x="24" y="28" text-anchor="middle" font-size="10" fill="white" font-weight="bold">DOC</text>
        </svg>`);

      case 'XLS':
        return this.sanitizer
          .bypassSecurityTrustHtml(`<svg width="48" height="48" viewBox="0 0 48 48" fill="none">
          <rect width="48" height="48" rx="12" fill="#059669"/>
          <path d="M14 16h20v16H14z" fill="white" fill-opacity="0.2"/>
          <text x="24" y="28" text-anchor="middle" font-size="10" fill="white" font-weight="bold">XLS</text>
        </svg>`);

      default:
        return this.sanitizer.bypassSecurityTrustHtml(
          `<svg width="26" height="32" viewBox="0 0 26 32" fill="none" xmlns="http://www.w3.org/2000/svg"> <g opacity="0.5" clip-path="url(#clip0_9617_19229)"> <path d="M17.4109 1.09814H4.17876C3.39889 1.09814 2.65097 1.41215 2.09953 1.97108C1.54808 2.53001 1.23828 3.28809 1.23828 4.07854V27.9217C1.23828 28.7121 1.54808 29.4702 2.09953 30.0291C2.65097 30.5881 3.39889 30.9021 4.17876 30.9021H21.8216C22.6015 30.9021 23.3494 30.5881 23.9008 30.0291C24.4523 29.4702 24.7621 28.7121 24.7621 27.9217V8.54912L17.4109 1.09814Z" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/> <path d="M15.9404 1.09814V7.05893C15.9404 7.84938 16.2502 8.60745 16.8017 9.16638C17.3531 9.72532 18.101 10.0393 18.8809 10.0393H24.7619" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/> </g> <defs> <clipPath id="clip0_9617_19229"> <rect width="26" height="32" fill="white"/> </clipPath> </defs> </svg>`
        );
    }
  }

  formatDate(date: string | undefined): string {
    if (!date) {
      return '';
    }
    let thisDate = new Date(date);
    const day = thisDate.getDate().toString().padStart(2, '0');
    const month = thisDate.toLocaleString('default', { month: 'short' });
    const year = thisDate.getFullYear();
    return `${day} ${month} ${year}`;
  }

  onFileClick(): void {
    this.fileClick.emit(this.file);
  }

  onMenuClick(event: Event): void {
    event.stopPropagation();
    this.menuClick.emit(this.file);
  }
}
