import { Component, EventEmitter, input, Input, Output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { checkIcon, infoSvg } from './assets';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Component({
  selector: 'custom-confirm-popup',
  imports: [CommonModule],
  templateUrl: './custom-confirm-popup.component.html',
  styleUrl: './custom-confirm-popup.component.css',
})
export class CustomConfirmPopupComponent {
  @Input({ required: true }) message: string = '';
  @Input({ required: true }) type: 'info' | 'delete' = 'info';
  @Input() confirmButtonText: string = 'YES';
  @Input() cancelButtonText: string = 'NO';
  @Input() extraButton!: string;
  @Output() confirmEvent = new EventEmitter<any>();
  @Output() cancelEvent = new EventEmitter<void>();
  @Output() extraEvent = new EventEmitter<void>();
  @Output() overlayClicked = new EventEmitter<boolean>(false);
  showSucessScreen = input<boolean>(false)
  sucessMsg = input<string>('')
  checkedInfoSvg: SafeHtml;
  checkIcon: SafeHtml;

  constructor(private sanitizer: DomSanitizer) {
    const infoSvgIcon = infoSvg;
    this.checkedInfoSvg = this.sanitizer.bypassSecurityTrustHtml(infoSvgIcon);
    this.checkIcon = this.sanitizer.bypassSecurityTrustHtml(checkIcon);
  }

  isVisible = false;
  eventVal: any;

  open(event?: any) {
    this.isVisible = true;
    this.eventVal = event;
  }

  close() {
    if(!this.showSucessScreen()) this.isVisible = false;
  }

  onOverlayClick(event: MouseEvent) {
    this.overlayClicked.emit(true)
    if (event.target === event.currentTarget) {
      this.isVisible = false;
    }
  }
}
